import React from "react";
import { BrowserRouter as Router, Switch, Route, Link, generatePath } from "react-router-dom";
import { ANOTHER_PATH, HOME_PATH } from "./pathnames";



const App = () => {
  return (
    <Router>
      
      <Link to={HOME_PATH} style={{marginRight: "1em"}}>home</Link>
      <Link to={ANOTHER_PATH} style={{marginRight: "1em"}}>another</Link>
      
      <Switch>
        <Route exact path={HOME_PATH} render={() => <div>Home Path</div>} />
        <Route exact path={ANOTHER_PATH} render={() => <div>Another Path</div>} />
        <Route path="*" render={() => <div>404</div>} />
      </Switch>
    
    </Router>
  );  
};

export default App;